@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
body,html {
  width: 100%;
  height: 100vh;
  margin: 0;
  font-family: 'Nunito', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  cursor: default;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Color Palettes */
:root{
  --app-background: #313740;
  --app-column-counter: #1e2128;
  --app-row-counter: #252a32;
  --app-menu: #36383d;
  --app-menu-text: #fbfbfb;
  --app-popup-top: #323942;
  --app-popup-text: #2c3039;
  --app-popup-background: #fefefe;
  --element-dimension: 68px;
}

*{
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100%;
  background: #313740;
  background: var(--app-background);
  color: #fbfbfb;
  color: var(--app-menu-text);
  overflow-x: hidden;
  overflow-y: auto;
}

/* centers */
.centroid {
  max-width: 1440px;
  margin: auto;
  padding: 10px;
}

.workarea{
  overflow-x: auto;
  padding: 15px;
  /* padding-right: 30px; */
}

.workarea * {
  transition: 0.3s ease;
  -webkit-transition: 0.3s ease;
}

.workarea-periodic-table{
  display: grid;
  place-items: center;
  overflow: auto;
  position: relative;
}


.periodic-table{
    display: grid;
    grid-template-columns: repeat(18, var(--element-dimension));
    grid-template-rows: repeat(10, var(--element-dimension));
    grid-gap: 4px;
  }
  
  .element{
    background: var(--app-menu);
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.3);
    border-radius: 2px;
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
  }
  
  .element-atomic-no{
    font-weight: 400;
    font-size: 10px;
    position: absolute;
    top: 7px;
    left: 7px;
    display: table;
    flex: 0 1;
  }
  
  .element-symbol{
    font-size: 25px;
    padding-top: 7px;
    font-weight: 700;
    padding-top: 15px;
    flex: 6 1;
  }
  
  .element-name{
    font-size: 9px;
    letter-spacing: 0.54px;
    flex: 3 1;
    text-transform: capitalize;
  }

  /* Legend */
.pt-legend {
  position: absolute;
  top: 7%;
  left: 230px;
  z-index: 5;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 12px;
}

.pt-legend-item {
  display: flex;
  cursor: pointer;
  border-radius: 5px;
  padding: 4px;
}

.pt-legend-item:hover {
  background: #fff;
  color: var(--app-menu);
  box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.3);
}

.pt-legend-color{
  width: 25px;
  height: 15px;
  border-radius: 5px;
  align-self: center;
}

.pt-legend-name{
  font-size: 10px;
  letter-spacing: 1px;
  padding-left: 10px;
  text-transform: uppercase;
  align-self: center;
  white-space: nowrap;
}

.cover {
  background: #fff;
  position: relative;
  z-index: 7;
  border-radius: 2px;
}


.navbar{
  height: 50px;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background: var(--app-popup-top);
  box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.3);
}

.navbar-item{
  align-self: center;
  margin: 0;
}

/* navbar hamburger */
.navbar-menu{
	width: 30px;
	height: 30px;
	align-items: center;
	justify-content: center;
	margin-right: 10px;
	cursor: pointer;
	position: relative;
}

.navbar-menu > .middle{
	width: 100%;
	height: 2px !important;
	background: var(--app-menu-text);
	transition: 0.3s ease;
	-webkit-transition: 0.3s ease;
  transform: translateY(13px);
}

.navbar-menu > .middle:before, .navbar-menu > .middle:after{
	content: '';
	width: 100%;
	height: 2px !important;
  background: var(--app-menu-text);
	position: absolute;
	transition: 0.3s ease;
	-webkit-transition: 0.3s ease;
}

.navbar-menu > .middle:before{
	transform: translateY(-400%);
}

.navbar-menu > .middle:after{
	transform: translateY(400%);
}

.menu-tapped{
	position: relative;
}

.menu-tapped .middle{
	background: transparent;
	transition: 0.01s ease;
}

.menu-tapped .middle:before{
	transform: rotate(45deg) translate(3%, -7%);
}

.menu-tapped .middle:after{
	transform: rotate(-45deg) translate(-0%, -3%);
}

/* navbar name */
.navbar-name{
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.navbar-search{
  width: 30px;
  height: 30px;
  display: flex;
}

.navbar-search img{
  width: 65%;
  object-fit: contain;
  align-self: center;
}
